import {Component, OnInit, ViewChild} from '@angular/core';
import {AssemblyMan, Period} from '../../../interfaces/model';
import {IonInfiniteScroll, ModalController, ToastController} from '@ionic/angular';
import {RestService} from '../../../service/rest.service';
import {ModalAsambleistaPage} from '../../modal/modal-asambleista/modal-asambleista.page';

@Component({
  selector: 'app-tab-principales',
  templateUrl: './tab-principales.page.html',
  styleUrls: ['./tab-principales.page.scss'],
})
export class TabPrincipalesPage implements OnInit {
    assemblyMan: AssemblyMan[] = [];
    searchAssembly = '';
    curulNumber = '';
    photoMan: any;
    periodActive: Period = new Period();
    allPeriod: Period[] = [];
    arrayPeriod: Period[] = [];
    bandCurul = false;
    periodSelect: Period =  new Period();
    i = 0;
    // @ts-ignore
    @ViewChild(IonInfiniteScroll) infiniteScroll: IonInfiniteScroll;
    constructor(public modalController: ModalController,
                public toastController: ToastController,
                private rest: RestService) {}

  ngOnInit() {
      this.getPeriod();
      this.getPeriodAll();
  }
  getPeriod() {
        this.rest.getPeriod().subscribe(respo => {
                this.periodActive =  respo;
                this.periodSelect = respo;
            }, error1 => {}, () => {
                this.listAssemblyByPeriod();
            }
        );
  }

  getPeriodAll() {
      let j = 0;
      this.rest.getPeriodAll().subscribe(response => {
              this.allPeriod = response;
              this.allPeriod.forEach(data => {
                  if (!data.name.indexOf('PERÍODO')) {
                      this.arrayPeriod[j] = data;
                      j = j + 1;
                  }
              });
          }, error1 => {}, () => {

          }
      );
  }
    onChange(event) {
        this.periodActive = event.target.value;
        this.assemblyMan = [];
        this.i = 0;
        this.infiniteScroll.disabled = false;
        this.listAssemblyByPeriod();
    }
    public loadData(event) {
        setTimeout(() => {
            this.listAssemblyByPeriod(event);
        }, 500);
    }
    listAssemblyByPeriod(event?) {
        this.infiniteScroll.disabled = false;
        let a = '0';
        let c = '0';
        if (this.searchAssembly !== '') {
            a = this.searchAssembly;
        }
        if (this.curulNumber.toString() !== '') {
            c = this.curulNumber;
        }
        if (this.periodActive.id === this.periodSelect.id) {
            this.bandCurul = false;
        } else {
            this.bandCurul = true;
        }
        const incre = 30;
        if (event !== undefined) {
            this.i = this.i + incre;
        }
        this.rest.getAssemblyMembersResource({
            periodId: this.periodActive.id,
            territorialId: 0,
            politicalPartyId: 0,
            includePicture: false,
            onlyActives: false,
            onlyAlterns: false,
            assembly: a,
            curul: c,
            offset: this.i,
            limit: 30
        }).subscribe( response => {
            if (response.length > 0) {
                if (event !== undefined) {
                    this.assemblyMan.push(...response);
                    if (event) {
                        event.target.complete();
                    }
                } else {
                    this.assemblyMan = response;
                }
            } else {
                if (response.length === 0 && event !== undefined) {
                    event.target.disabled = true;
                }
            }
        }, () => {
            console.log('Ha ocurrido un error');
        }, () => {
            if (this.assemblyMan) {
                this.listAssemblyPhoto();
            }
        });
    }

    listAssemblyPhoto() {
        this.assemblyMan.forEach((data) => {
            this.getPhoto(data);
        });
    }

    getPhoto(data: AssemblyMan) {
        this.rest.getAssemblymanPhoto({
            assemblyMemberId: data.id
        }).subscribe( responsePhoto => {
                const blob = new Blob([responsePhoto], {
                    type: 'image/png'
                });
                if (blob.size > 0) {
                    const reader = new FileReader();
                    reader.readAsDataURL(blob);
                    reader.onloadend = () => {
                        this.photoMan = reader.result;
                        data.imageChange = this.photoMan;
                    };
                } else {
                    data.imageChange = '../assets/images/silueta.png';
                }
            }, () => {
                data.imageChange = '../assets/images/silueta.png';
            }
        );

    }

    async changeModal(id, image) {
        const modal = await this.modalController.create({
            component: ModalAsambleistaPage,
            componentProps: {
                periodActive: this.periodActive.id,
                idAssembly: id,
                imageChange: image,
                assembly: this.assemblyMan.filter(data => data.id === id),
                bandActive: this.bandCurul
            }
        });
        return await modal.present();
    }

    /*searchItems(event) {
        const f = event.target.value;
        if ( f && f.toString().length > 2 ) {
            this.rest.getAssemblyMembersResource({
                periodId: this.periodActive.id,
                territorialId: 0,
                politicalPartyId: 0,
                includePicture: false,
                onlyActives: false,
                onlyAlterns: false,
                assembly: f,
                offset: 0,
                limit: 30
            }).subscribe( response => {
                this.assemblyMan = response;
            }, () => {
            }, () => {
                if (this.assemblyMan) {
                    this.listAssemblyPhoto();
                    this.infiniteScroll.disabled = true;
                }
            });
        } else if (this.searchAssembly === '') {
            this.assemblyMan = [];
            this.listAssemblyByPeriod();
        }
    }*/

    resetTextFirst() {
        this.searchAssembly = '';
        this.i = 0;
        this.assemblyMan = [];
        this.listAssemblyByPeriod();
    }
    resetTextSecond() {
        this.curulNumber = '' ;
        this.i = 0;
        this.assemblyMan = [];
        this.listAssemblyByPeriod();

    }
    getClear() {

            this.i = 0;
            this.assemblyMan = [];
            this.listAssemblyByPeriod();

        }

    async presentToast(msg) {
        const toast = await this.toastController.create({
            message: msg,
            duration: 2000,
            position: 'bottom'
        });
        toast.present();
    }
    messageClose() {
        this.toastController.dismiss({
            'dismissed': true
        });
    }
}
